/**
 * Console Controller
 */

import ConsoleView from './view';

export default class Console {
    constructor() {
        this.view = new ConsoleView();
    }

    log(...args) {
        this.view.addRow({
            time: new Date(),
            msg: args
                .map(arg => {
                    return typeof arg === 'object'
                        ? JSON.stringify(arg, null, ' ')
                        : arg.toString()
                })
                .join(' ')
        });
    }

    clear() {
        this.view.clear();
    }
}



