/**
 * TV Application Parameters View
 */

import View from '../common/view';

export default class ParamsView extends View{
    constructor() {
        super();
        this.domElements = [
            {key: 'tvIp', domId: 'tv-ip'},
            {key: 'appId', domId: 'app-id'},
            {key: 'jsUrl', domId: 'js-url'},
            {key: 'cssUrl', domId: 'css-url'},
            {key: 'iframeUrl', domId: 'iframe-url'}
        ];
        this._assignDomElementsGetters();
    }
}



