/**
 * HTTP Client
 */

import axios from 'axios';

export default class Http {
    /**
     * @param {Object} options
     * @param {Console} options.console
     */
    constructor(options) {
        this.console = options.console;
    }

    get(url) {
        this.console.log(`GET ${url}`);
        return axios.get(url)
            .then(resp => this._logResponse(resp))
            .catch(error => this.console.log(error.message));
    }

    post(url, data, config) {
        this.console.log(`POST ${url}`, data);
        return axios.post(url, data, config)
            .then(resp => this._logResponse(resp))
            .catch(error => this.console.log(error.message));
    }

    delete(url) {
        this.console.log(`DELETE ${url}`);
        return axios.delete(url)
            .then(resp => this._logResponse(resp))
            .catch(error => this.console.log(error.message));
    }

    _logResponse(resp) {
        this.console.log(resp.status, resp.statusText, resp.data);
        return resp;
    }
}



