/**
 * TV Application Parameters Controller
 */

import assign from 'lodash/assign';
import ParamsView from './view';
import defaultValues from './defaults';

const localStorageKey = 'yandex.tv-app-launcher.params.default-values';

export default class Params {
    constructor() {
        this.view = new ParamsView();
        this.values = assign(defaultValues, getLocalStorageDefaults());
        document.addEventListener('DOMContentLoaded', () => this.renderDefaults());
    }

    renderDefaults() {
        Object.keys(this.values).forEach(key => {
            if (this.view[key]) {
                this.view[key].value = this.values[key];
            }
        });
    }

    bindEvents() {
        const view = this.view;
        view.domElements.forEach(({key}) => {
            view[key].addEventListener('change', () => {
                this.values[key] = view[key].value;
                localStorage.setItem(localStorageKey, JSON.stringify(this.values));
            });
        });
    }
}

function getLocalStorageDefaults() {
    try {
        return JSON.parse(localStorage.getItem(localStorageKey));
    }  catch (e) {
        return {};
    }
}



