/**
 * TV Application Controller
 */

import {wait} from '../common/utils';
import Http from './http';

export default class TvApp {
    /**
     * @param {Object} options
     * @param {String} options.ip
     * @param {String} options.id
     * @param {Console} options.console
     */
    constructor(options) {
        this.ip = options.ip;
        this.id = options.id;
        this.console = options.console;
        this.http = new Http({console: this.console});
    }

    get applicationUrl() {
        return `http://${this.ip}:8001/api/v2/applications/${this.id}`;
    }

    get tvInfoUrl() {
        return `http://${this.ip}:8001/api/v2/`;
    }

    /**
     * @param {Object} params
     * @param {String} params.js
     * @param {String} params.css
     * @param {String} params.content
     * @returns {Promise}
     */
    start(params) {
        this.console.log('Starting application...');
        const postData = JSON.stringify({
            id: JSON.stringify(params)
        }, null, ' ');
        const headers = {
            'Content-Type': 'application/json'
        };
        return this.stop()
            .then(() => this.http.post(this.applicationUrl, postData, {headers}));
    }

    stop() {
        this.console.log('Stopping application...');
        return this.status()
            .then(data => {
                if (data && data.running) {
                    return this.http.delete(this.applicationUrl)
                        .then(() => this.console.log('Waiting 1500ms...'))
                        .then(() => wait(1500))
                        .then(() => this.console.log('waiting finished.'));
                }
            });
    }

    info() {
        this.console.log('Retrieving tv info...');
        return this.http.get(this.tvInfoUrl);
    }

    status() {
        this.console.log('Retrieving application status...');
        return this.http.get(this.applicationUrl)
            .then(resp => {
                return !resp.data ? Promise.reject() : resp.data;
            })
            .catch(() => this.console.log('Error: cannot retrieve application status'));
    }
}



