/**
 * Buttons Controller
 */
import Console from '../console'
import TvApp from '../tv-app';
import ButtonsView from "./view";

export default class Buttons {
    /**
     * @param {Object} options
     * @param {Params} options.params
     */
    constructor(options) {
        this.params = options.params;
        this.view = new ButtonsView();
        this.console = new Console();
    }

    bindEvents() {
        this.view.startBtn.addEventListener('click', event => this._handleStartBtnClick(event));
        this.view.stopBtn.addEventListener('click', event => this._handleStopBtnClick(event));
        this.view.infoBtn.addEventListener('click', event => this._handleInfoBtnClick(event));
        this.view.clearBtn.addEventListener('click', () => this.console.clear());
    }

    _handleStartBtnClick() {
        this._tvApp.start({
            js: this.params.values.jsUrl,
            css: this.params.values.cssUrl,
            content: this.params.values.iframeUrl
        });
    }

    _handleStopBtnClick() {
        this._tvApp.stop()
    }

    _handleInfoBtnClick() {
        this._tvApp.info();
    }

    get _tvApp() {
        return new TvApp({
            ip: this.params.values.tvIp,
            id: this.params.values.appId,
            console: this.console
        });
    }
}



