/**
 * Buttons View
 */
import View from '../common/view';

export default class ButtonsView extends View {
    constructor() {
        super();
        this.domElements = [
            {key: 'startBtn', domId: 'start-btn'},
            {key: 'stopBtn', domId: 'stop-btn'},
            {key: 'infoBtn', domId: 'info-btn'},
            {key: 'clearBtn', domId: 'clear-btn'}
        ];
        this._assignDomElementsGetters();
    }
}



