/**
 * DOM View
 */

export default class View {
    constructor() {
        this.domElements = [];
    }

    _assignDomElementsGetters() {
        this.domElements.forEach(el => {
            Object.defineProperty(this, el.key, {
                get: function () {
                    return this[`_${el.key}`] || (this[`_${el.key}`] = document.getElementById(el.domId));
                }
            });
        });
    }
}



