/**
 * Main application class
 */
import Params from './params';
import Buttons from './buttons';

export default class App {
    constructor() {
        this.params = new Params();
        this.buttons = new Buttons({
            params: this.params
        });
    }

    run() {
        this.buttons.bindEvents();
        this.params.bindEvents();
    }
}



