/**
 * Console View
 */

import View from '../common/view';

export default class ConsoleView extends View {
    constructor() {
        super();
        this.domElements = [
            {key: 'rowTmpl', domId: 'console-row-tmpl'},
            {key: 'console', domId: 'console'}
        ];
        this._assignDomElementsGetters();
    }

    /**
     * @param {Object} data
     * @param {Date} data.time
     * @param {String} data.msg
     */
    addRow({time, msg}) {
        const row = document.importNode(this.rowTmpl.content, true);
        row.querySelector('.console__row-time').innerHTML = formatTime(time);
        row.querySelector('.console__row-body').innerHTML = msg;
        this.console.appendChild(row);
        this.console.scrollTop = this.console.scrollHeight;
    }

    clear() {
        const rows = [...this.console.querySelectorAll('.console__row')];
        rows.forEach(row => row.parentNode.removeChild(row));
    }
}

function formatTime(time) {
    const hours = time.getHours().toString().padStart(2, '0');
    const mins = time.getMinutes().toString().padStart(2, '0');
    const secs = time.getSeconds().toString().padStart(2, '0');
    const ms = time.getMilliseconds().toString().padStart(3, '0');
    return `${hours}:${mins}:${secs}.${ms}`;

}



